import { Injectable } from '@angular/core';
import * as JsCrypto from "jscrypto/es6";
import { Data } from './sharing/models/master.model';
@Injectable({
  providedIn: 'root'
})
export class AppService {
  secretKey: string = "SH256AdminEncryption"
  constructor() { }
  getAdminDetails() : Data {
    return localStorage.getItem("_A") ? JSON.parse(this.decrypt(localStorage.getItem("_A"))) : "";
  }

  getAdminUserRoleDetails() {
    return localStorage.getItem("_A") ? JSON.parse(this.decrypt(localStorage.getItem("_A"))).user.admin_role : "";
  }

  setStorageData(key,data : any) {
    localStorage.setItem(key, this.encrypt(JSON.stringify(data)));
  }

  getStorageData(key : string){
    return localStorage.getItem(key) ? JSON.parse(this.decrypt(localStorage.getItem(key))) : "";
  }

  encrypt(value: string): string {
    return JsCrypto.AES.encrypt(value, this.secretKey.trim()).toString();
  }

  decrypt(textToDecrypt: string) {
    return JsCrypto.AES.decrypt(textToDecrypt, this.secretKey.trim()).toString(JsCrypto.Utf8);
  }
}
